#container {
  width: 100%;
  height: calc(100vh - 150px);
  /* border: solid 1px black; */
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}
#wrapper {
  /* border: solid 1px red; */
  padding: 10px;


  display: flex;
  justify-content: center;
}
#wrapper #message {
    border: solid 1px black;
    text-align: center;

}
#wrapper img {
  height: 250px;
  width: 250px;
  /* border: solid 1px black; */
}

#wrapper h1 {
  /* border: solid 1px black; */
  font-size: 17px;
  font-weight: 800;
  text-align: center;
  text-transform: uppercase;
  margin-top: 20px;
  margin-bottom: 30px;
}
#wrapper button {
  width: 100%;

  display: block;
  border: solid 1px black;
  padding: 5px 20px;
  margin-top: 10px;
  font-size: 20px;
  background-color: transparent;
  text-transform: uppercase;
  border-radius: 10px;
}

#wrapper #score {


}
#wrapper #rules {
  color: white;
  background-color: black;


}
