#main {
  width: 100%;
  height: 96.6vh;
  background-color: whitesmoke;

  display: flex;
  justify-content: center;
  align-items: center;
}

#container {
  width: 80%;
  height: 60vh;
  /* border: solid 1px black; */
  display: flex;
}
#container .left {
  width: 50%;
  /* border: solid 1px black; */
  height: 100%;
}
#container .left  img {
    /* height: 487px;
   width: 474px; */
}
#container .right {
  width: 50%;
  /* border: solid 1px black; */
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-right: 20px;

}
#container .right h1{
    font-size: 70px;
    text-transform: uppercase;

}
#container .right .btnWrapper {
    display: flex;
    justify-content: flex-end;

}
#container .right .btnWrapper button {
   border: none;
   font-size: 20px;
   background-color: black;
   color: white;
   padding: 10px 30px;
  

}
#container .right .btnWrapper button:hover {
    cursor: pointer;

}
